<div class="container"><div class="row justify-content-center"><div class="col-md-6 mx-auto"><form @submit.prevent="login"><div class="row"><div class="col-md-8 mx-auto"><div class="form-group mb-2"><label for="login-email">Email</label><input class="form-control" v-model="email" type="text" id="login-email" placeholder="Enter email"></div></div></div><div class="row"><div class="col-md-8 mx-auto"><div class="form-group"><label for="login-password">Password</label><input class="form-control" v-model="password" type="password" id="login-password" placeholder="Enter password"></div></div></div></form></div></div></div>