<div class="home"><h1 v-if="loggedIn">Welcome, {{user.username}}!<div class="col-md-8 mx-auto mt-3"><button class="btn btn-outline-danger" type="button" @click="logoutUser">Logout</button></div></h1><div v-else><h3>Log In</h3><LogInForm ref="loginForm"></LogInForm><div class="col-md-8 mx-auto mt-3"><button class="btn btn-primary me-3" type="button" data-bs-toggle="modal" data-bs-target="#signUpModal">Sign Up</button><button class="btn btn-primary" type="button" @click="login">Login</button></div><div class="modal fade" tabindex="-1" id="signUpModal"><div class="modal-dialog"><div class="modal-content"><div class="modal-header"><h5 class="modal-title">Sign Up</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><SignUpForm></SignUpForm></div></div></div></div></div></div>