<form @submit.prevent="createUser"><div class="form-group"><label for="email">Email</label><input class="form-control" id="email" type="email" v-model="email" placeholder="Enter email"></div><div class="form-group"><label for="password">Password</label><input class="form-control" id="password" type="password" v-model="password" placeholder="Enter password"></div><div class="form-group"><label for="username">Username</label><input class="form-control" id="username" type="text" v-model="username" placeholder="Enter username"></div><button class="btn btn-primary" type="submit">Sign Up</button></form>