<div><div v-if="documents &amp;&amp; documents.length &gt; 0"><h3 class="form-label">PDF List</h3><ul class="list-group container"><li class="flex-column align-items-start" v-for="document in documents" :key="document._id" :class="{ 'list-group-item': true }"><div class="text-center row"><div class="col d-flex align-items justify-content-between"><a @click.prevent="openModal(document)"><button class="btn btn-dark btn-lg shadow-sm me-4">{{ document.title }}</button><p class="text-wrap fs-6 d-inline-flex">{{ document.description }}</p></a></div><button class="col-3 btn btn-outline-danger shadow-sm d-flex flex-row" type="button" @click="confirmDelete(document._id, document.title)">Delete</button></div></li><mDocumentPopout class="modal-fade" role="dialog" :key="selectedId" :documentId="selectedId"></mDocumentPopout></ul></div></div>