<div class="modal fade show d-block" v-if="showModal" id="docDisplay" tabindex="-1" role="dialog" aria-labelledby="docDisplayLabel" aria-hidden="false"><div class="modal-dialog modal-lg border border primary rounded" role="document"><div class="modal-content"><h5 class="modal-title m-3" id="docDisplayLabel">{{ currentTitle }}<button class="close" type="button" @click="closeModal" aria-label="Close"><span aria-hidden="true">&times;</span></button></h5><div class="modal-body"><vue-pdf-embed :source="pdfSrc" width="600" height="500"></vue-pdf-embed></div><div class="modal-footer"></div><form>   <div class="row mb-3"><div class="col"><label for="updateTitleInput">Update Document Title</label><input class="form-control" v-model="updateData.title" id="updateTitleInput" :placeholder="currentTitle"></div><div class="col"><label for="updateDescriptionInput">Update Document Description</label><input class="form-control" v-model="updateData.description" id="updateDescriptionInput" :placeholder="currentDescription"></div></div><div class="row mb-3"><div class="col"><button class="btn btn-secondary" type="button" @click="closeModal">Close</button></div><div class="col"><button class="btn btn-success" type="button" @click="updateDocument">Save Changes</button></div></div></form></div></div></div>