<div class="container"><div class="row justify-content-center"><div class="col-md-6"><div class="form-group"><label for="documentTitle">Title of document</label><input class="form-control" id="documentTitle" placeholder="Enter Title" v-model="document.title"><small class="form-text text-muted" id="titleTag">A default roman numeral titled document will be generated otherwise</small></div><div class="form-group"><label for="documentDescription">Description of Document</label><input class="form-control" id="documentDescription" placeholder="Enter Description" v-model="document.description"><small class="form-text text-muted" id="titleTag">A default description will be generated otherwise</small></div><div class="form-group"><select class="form-control" :class="{ 'is-invalid': isCategoryInvalid }" id="category" v-model="document.category" required><option disabled value="">--Select Category--</option><option v-for="category in categories" :key="category">{{ category }}</option></select></div><div class="form-group"><button class="btn btn-success" @click="submitForm">Submit </button></div></div></div></div>